#settings > .Panel__in {
    background-color: var(--background-block);
}

.settingsTitleContainer {
    padding: 0 0 16px 0;
}

.settingsTitle {
    font-size: 14px;
    color: #5690ff;
}

.groupSettingsContainer {
    background-color: var(--background);
    box-shadow: 0 4px 4px 0 #26000033;
    padding: 15px 0 12px 16px;
    margin-bottom: 12px;
    /*display: flex;*/
    /*flex-direction: column;*/
    /*align-items: center;*/
}

.groupSettingsContainer > button {
    margin-top: 0 !important;
    padding: 0;
}

.settingsSettingContainer {
    width: 100%;
    min-height: 30px;
    /*padding: 0 0 0 16px;*/
    background-color: var(--background);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    border-radius: 0 !important;
}

.settingsSettingContainer > div {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.settingsSettingContainer > div > .Button__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
}

.settingsSettingContainer > div > div {
    color: var(--text-color);
    font-size: 17px !important;
}

.settingsSettingInfo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 30px;
    height: 100%;
    width: 100%;
    padding: 3px 0 3px 0;
    margin-left: 15px;
}

.settingsSettingTitle {
    display: flex;
    flex-direction: row;
    font-size: 16px;
    font-weight: normal !important;
    color: var(--text-color);
}

.settingsSettingSwitch {
    margin-right: 16px;
}

#settingsGroupSubscribe {
    background-color: var(--additional-background-block);
    color: var(--button-secondary-text)
}


.settingsSettingSwitcher {
    margin-right: 16px;
}

.settingsButtonsSeparator {
    display: flex;
    width: 75%;
    border: 1px solid var(--additional-background-block);
    margin-left: 12%;
}